<template>
  <div class="clock-container">
    <transition name="fade appear" 
      enter-active-class="animated zoomIn"
      leave-active-class="animated bounceOutRight">
        <div v-if="show" class="clock-date" style="transition-delay: 0.3s">{{clockData.date}}</div>
    </transition>
    <transition name="fade appear"
      enter-active-class="animated zoomIn"
      leave-active-class="animated bounceOutRight">
        <div class="clock-devider" v-if="show"  style="transition-delay: 0.6s"></div>
    </transition>
    <transition name="fade appear"
     >
        <Clock1 v-if="show"></Clock1>
    </transition>

  </div>
</template>

<script>
import Clock1 from './Clock1.vue';

export default {
  name: 'Clock',
  components: {
      Clock1
  },
  data () {
    return {
        clockData: {
            date: 'Monday 1 March 2021'
        },
        timerInterval: null,
        show: false
    }
  },

  methods: {
      getdateString: function() {
        var d = new Date();
        var weekday = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
        var month = ["January","February","March","April","May","June","July","August","September","October", "November", "December"];
        var nwd = weekday[d.getDay()];
        var nm = month[d.getMonth()];
        return nwd+' '+d.getDate()+' '+nm +' '+d.getFullYear();
      }

  },
  mounted: function () {
        this.clockData.date = this.getdateString();  
        this.timerInterval = setInterval(() => {
            this.clockData.date = this.getdateString();    
        }, 1000*60);
        setTimeout(() => {
            this.show = true;
        }, 500);
        
  }
}
</script>

