.clock-container {
    width: 320px;
    float: right;
    max-width: 100%;
    .clock-date {
        font-size: 30px;
        font-weight: bold;
        color: #fff;
        text-shadow: 0px 0px 5px #a3ab3194;
    }
    .clock-devider {
        width: 150px;
        max-width: 90%;
        height: 3px;
        /* margin-top: 8px; */
        background: whitesmoke;
        margin: auto;
        box-shadow: 0px 0px 5px #a3ab3194;
    }
  @media (max-width: 500px) {
    transform: scale(0.75);
    transform-origin: right top;
  }   
}

</style>
