<template>
  <div class="gsearch-container">
    <button class="ob-button ob-small rotate-hover" key="1" ref="gsearchbt" content="Search" v-tippy='{placement:"right"}'  @click="gsearchData.gsearchPanel.show = !gsearchData.gsearchPanel.show" ><i class="icon las la-search"></i></button>
      <div class="gsearch-bar ob-modal" v-bind:style="gsearchStyle" ref="gsearchpn">
        <input class="gsearch-input" type="text" v-model="gsearchData.gsearch" v-on:keyup.enter="search"/>
      </div>
  </div>
</template>

<script>

import appMixin from '../../mixins/appMixin';

export default {
  name: 'gsearch',
  mixins: [appMixin],
  components: {
  },
  data () {
    return {
        gsearchData: {
          gsearchPanel: {show: false, top: 110, left: 100, width: 300, height: 20},
          gsearch: ''
        },
        test: '',
        gsearchStyle: '',
       
        
        
    }
  },

  methods: {
   search: function() {
     let url = 'https://www.google.com/search?q='+this.gsearchData.gsearch;
     window.open(url,'_blank');
     let parsed = JSON.stringify(this.gsearchData);
      localStorage.setItem('gsearchData', parsed);
   }
        
  },
  watch: {
  // whenever question changes, this function will run
    'gsearchData.gsearchPanel.show': {
      handler (newAppData, oldAppData) {
        this.gsearchStyle = this.caculateModal('gsearchbt','gsearchpn',this.gsearchData.gsearchPanel);
      }
    },
    
  },
  mounted: function () {
    setTimeout(() => {
      if (localStorage.getItem('gsearchData')) {
        try {
          let localData = JSON.parse(localStorage.getItem('gsearchData'));
          for (var name in localData) {
          //  alert(name);
            this.gsearchData[name] = localData[name];
          }
          let topleft = this.calculateTopLeft('gsearchbt',this.gsearchData.gsearchPanel);
          this.gsearchData.gsearchPanel.top = topleft.top;
          this.gsearchData.gsearchPanel.left = topleft.left;
          //this.gsearchData = JSON.parse(localStorage.getItem('gsearchData'));
        } catch(e) {
          localStorage.removeItem('gsearchData');
        }
      }
    }, 100);

    
  }
}
</script>

<style lang="scss">


.gsearch-container {
  .gsearch-bar {
    background: transparent;
    box-shadow: none;
    
    .gsearch-input {
        background: #67676775;
        color: #FFF;
        font-weight: 580;
    }
  }

}



</style>
