<template>
  <div id="app">
    <div class="main" id="main">
      <transition name="slide-fade">
  <!-- 			SRC comes from the array of images the :key is important for vue to believe its a 'new' DOM element and do the transition -->
        <div class="theImage" v-bind:style="{'background-image': 'url(' + appData.currentImage.url + ')'}" v-bind:key="appData.currentImage.id"></div>
      </transition>
      <div class="image-info">
        <!-- <div class="image-description"><a v-bind:href="appData.currentImage.link" target="_blank"> {{appData.currentImage.description}}</a></div> -->
        <div class="image-author">
          <a v-if="appData.currentImage.location && appData.currentImage.location.name" 
            v-bind:href="'https://www.google.com/maps/place/'+appData.currentImage.location.name" target="_blank" 
            v-text="appData.currentImage.location.name"></a><br/>
          <a v-bind:href="appData.currentImage.link" target="_blank">Photo</a> by <a v-bind:href="'https://unsplash.com/@'+appData.currentImage.username+'?utm_source=onboard&utm_medium=referral'" target="_blank">
          {{appData.currentImage.author}}</a> on <a href="https://unsplash.com/?utm_source=onboard&utm_medium=referral" target="_blank">Unsplash</a>
        </div>
      </div>
      <div class="effect-container">
        <Firefly v-if="appData.showEffect && effects[appData.effectIndex] == 'Firefly'"></Firefly>
        <Particle v-if="appData.showEffect && effects[appData.effectIndex] == 'Particle'"></Particle>
        <Star v-if="appData.showEffect && effects[appData.effectIndex] == 'Star'"></Star>
        <Bar v-if="appData.showEffect && effects[appData.effectIndex] == 'Bar'"></Bar>
        <Box v-if="appData.showEffect && effects[appData.effectIndex] == 'Box'"></Box>
      </div>
      <div class="top-right-container">
        <transition name="fade-move" appear>
          <Clock v-if="appData.showClock"></Clock>
        </transition>
        <transition name="fade-move" appear>
          <Weather v-if="appData.showWeather"></Weather>
        </transition>
        <transition name="fade-move" appear>
          <Goal v-if="appData.showGoal"></Goal>
        </transition>
        <transition name="fade-move" appear>
          <Quotes v-if="appData.showQuotes" v-bind:auto-quotes="appData.autoQuotes"></Quotes>
        </transition>
      </div>
      <div class="top-left-container">
        <transition name="fade-move" appear>
          <Gsearch v-if="appData.showGsearch"></Gsearch>
        </transition>
        <transition name="fade-move" appear>
          <Note v-if="appData.showNote"></Note>
        </transition>
        <transition name="fade-move" appear>
          <Cembed v-if="appData.showEmbed"></Cembed>
        </transition>
      </div>
      <div class="ob-modal" v-bind:style="settingStyle" ref="settingpn">
        <div class="ob-m-header">
          <i class="ob-m-icon las la-cog"></i>
          <span class="ob-m-title">Settings</span>
          <button class="ob-m-close rotate-hover" @click="appData.settingPanel.show = !appData.settingPanel.show"><i class="icon las la-times"></i></button>
        </div>
        <div class="ob-m-content">
          <div class="ob-input-group">
            <label class="ob-label">Background subjects</label>
            <input required='' type='text' v-model="appData.query" @change="resetPhotos">
            
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Show effect</label>
            <div>
              <toggle-button :value="appData.showEffect" v-model="appData.showEffect"
                color="#386dc5"
               :sync="true"
               :labels="{checked: 'Yes', unchecked: 'No'}"/>
            </div>
          </div>
          
          <div class="ob-input-group">
            <label class="ob-label">Show clock</label>
            <div>
              <toggle-button :value="appData.showClock" v-model="appData.showClock"
                color="#386dc5"
               :sync="true"
               :labels="{checked: 'Yes', unchecked: 'No'}"/>
            </div>
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Show weather</label>
            <div>
              <toggle-button :value="appData.showWeather" v-model="appData.showWeather"
                color="#386dc5"
               :sync="true"
               :labels="{checked: 'Yes', unchecked: 'No'}"/>
            </div>
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Show quote</label>
            <div>
              <toggle-button :value="appData.showQuotes" v-model="appData.showQuotes"
                color="#386dc5"
               :sync="true"
               :labels="{checked: 'Yes', unchecked: 'No'}"/>
            </div>
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Show goal</label>
            <div>
              <toggle-button :value="appData.showGoal" v-model="appData.showGoal"
                color="#386dc5"
               :sync="true"
               :labels="{checked: 'Yes', unchecked: 'No'}"/>
            </div>
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Show note</label>
            <div>
              <toggle-button :value="appData.showNote" v-model="appData.showNote"
                color="#386dc5"
               :sync="true"
               :labels="{checked: 'Yes', unchecked: 'No'}"/>
            </div>
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Show search</label>
            <div>
              <toggle-button :value="appData.showGsearch" v-model="appData.showGsearch"
                color="#386dc5"
               :sync="true"
               :labels="{checked: 'Yes', unchecked: 'No'}"/>
            </div>
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Auto rotate effects(mins): {{appData.autoEffect}}</label>
            <input type="range" min="0" max="100" value="0" v-model="appData.autoEffect">
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Auto rotate background(mins): {{appData.autoBg}}</label>
            <input type="range" min="0" max="100" value="0" v-model="appData.autoBg">
          </div>
          <div class="ob-input-group">
            <label class="ob-label">Auto rotate quotes(mins): {{appData.autoQuotes}}</label>
            <input type="range" min="0" max="100" value="0" v-model="appData.autoQuotes">
          </div>

        </div>
      </div>
      <div class="main-setting">
        <transition-group name="custom-classes-transition"
        enter-active-class="animated bounceInRight"
        leave-active-class="animated bounceOutRight">
          <button v-if="show" class="ob-button ob-small rotate-hover" key="1" @click="toggleImage" content="Change background" v-tippy='{placement:"left"}'><i class="icon las la-image"></i></button>
          <button v-if="show" class="ob-button ob-small rotate-hover" key="2" @click="toggleEffect" content="Change effect" v-tippy='{placement:"left"}'><i class="icon las la-magic"></i></button>
          <button v-if="show" class="ob-button ob-small rotate-hover" key="3" ref="settingbt" @click="appData.settingPanel.show = !appData.settingPanel.show" content="Settings" v-tippy='{placement:"left"}' ><i class="icon las la-cog"></i></button>
        </transition-group>
      </div>


    </div>
    <textarea class="gen-text" v-if="showGeneratePhoto" v-model="collectionJson"></textarea>

  </div>
</template>

<script>
import { createApi } from 'unsplash-js';
import appMixin from './mixins/appMixin';
import Firefly from './effects/Firefly.vue';
import Particle from './effects/Particle.vue';
import Star from './effects/Star.vue';
import Bar from './effects/Bar.vue';
import Box from './effects/Box.vue';
import Clock from './plugins/clock/Clock.vue';
import Weather from './plugins/weather/Weather.vue';
import Quotes from './plugins/quotes/Quotes.vue';
import Goal from './plugins/goal/Goal.vue';
import Note from './plugins/note/Note.vue';
import Cembed from './plugins/embed/Cembed.vue';
import Gsearch from './plugins/gsearch/Gsearch.vue';


const unsplash = createApi({ accessKey: '5c2becff0e349ff22732e1ad1f2ebe1bbd9c5533edac583afba84d465bfc38a5' });

const heavyImage = new Image();

export default {
  name: 'app',
  mixins: [appMixin],
  components: {
    Firefly,
    Particle,
    Star,
    Bar,
    Box,
    Clock,
    Weather,
    Quotes,
    Goal,
    Note,
    Cembed,
    Gsearch
  },
  data () {
    return {
      effects: ['Firefly','Particle','Star','Bar','Box'],
      appData:{
        currentImage: {id: 'abc', url:'https://images.freeimages.com/images/large-previews/efb/lotus-flower-1382251.jpg',link:'#',description:'Flower',author:'Unknown'},
        images: [],
        page: 1,
        query: '',
        effectIndex: 0,
        settingPanel: {show: false, top: 300, left: 300, width: 300, height: 400},
        autoBg: 5,
        autoEffect: 0,
        autoQuotes:6,
        showEffect: false,
        showClock: true,
        showWeather: true,
        showQuotes: true,
        showGoal: true,
        showNote: true,
        showEmbed: true,
        showGsearch: true,
      },
      show: false,
      settingStyle: '',
      autoBgInterval: null,
      autoEffectInterval: null,
      genList: [],
      showGeneratePhoto: false
    }
  },
  watch: {
    // whenever question changes, this function will run
    appData: {
      handler (newAppData, oldAppData) {
        console.log('some changed');
        const parsed = JSON.stringify(newAppData);
        localStorage.setItem('appData', parsed);
      },
      deep: true
    },
    'appData.settingPanel.show': {
      handler (newAppData, oldAppData) {
        this.settingStyle = this.caculateModal('settingbt','settingpn',this.appData.settingPanel);
      }
    },
    'appData.autoEffect': {
      handler (newAppData, oldAppData) {
        clearInterval(this.autoEffectInterval);
        if (newAppData > 0) {
          this.autoEffectInterval = setInterval(() => {
            this.toggleEffect();
            console.log('timer effect: '+this.appData.autoEffect);
          }, this.appData.autoEffect*1000*60)
        }
      }
    },
    'appData.autoBg': {
      handler (newAppData, oldAppData) {
        clearInterval(this.autoBgInterval);
        if (newAppData > 0) {
          this.autoBgInterval = setInterval(() => {
            this.toggleImage();
            console.log('timer BG: '+this.appData.autoBg);
          }, this.appData.autoBg*1000*60)
        }
      }
    }
  },
  computed: {
    collectionJson: function () {
      return JSON.stringify(this.genList);
    }
    
  },
  mounted: function () {
    if (localStorage.getItem('appData')) {
      try {
        let localData = JSON.parse(localStorage.getItem('appData'));
        for (var name in localData) {
        //  alert(name);
          this.appData[name] = localData[name];
        }
        //this.appData = JSON.parse(localStorage.getItem('appData'));
      } catch(e) {
        localStorage.removeItem('appData');
      }
    }

    if(this.appData.images.length < 2) {
      this.getPhotos();
    }
    var v = this;
    setTimeout(function () {
        v.show = true;
    }, 1000);
    
    this.appData.settingPanel.top = window.innerHeight - this.appData.settingPanel.height - 30;
    this.appData.settingPanel.left = window.innerWidth - this.appData.settingPanel.width - 70;
    let uri = window.location.search.substring(1); 
    let params = new URLSearchParams(uri);
    console.log(params.get("r"));
    if (params.get("r") == 'generatePhoto' && params.get("id") != null) {

      unsplash.collections.getPhotos({  collectionId:  params.get("id"), perPage: 500, page: 1, orientation: 'landscape' }).then(result => {
        if (result.errors) {
          // handle error here
          console.log('error occurred: ', result.errors[0]);
        } else {
          const feed = result.response;
          const { total, results } = feed;
          for (let i =0; i< results.length; i++) {
            unsplash.photos.get({ photoId: results[i].id }).then(info => {
              this.genList.push({
                id: results[i].id,
                url: results[i].urls.raw,
                description: results[i].alt_description,
                link: results[i].links.html,
                author: results[i].user.name,
                username: results[i].user.username,
                location: info.response.location});

            });
          }

          this.showGeneratePhoto = true;
        }
      });
        
    }
  },

  methods: {
    // caculateModal: function(button,panel,setting) {
    //   if (setting.show) {
    //     let btleft = setting.left;
    //     let bttop = setting.top;
    //     if (setting.top+setting.height > window.innerHeight) {
    //       bttop = 0;
    //     }
    //     if (setting.left+setting.width > window.innerWidth) {
    //       btleft = 0;
    //     }
    //     return 'transform: scale(1); opacity: 1; top: '+bttop + 'px; left: '+btleft + 'px; width: '+setting.width+'px;height: '+setting.height+'px;';
    //   } else {
    //     let btposition = this.$refs[button].getBoundingClientRect();
    //     let btleft = btposition.x + btposition.width/2;
    //     let bttop = btposition.y + btposition.height/2;
    //     return 'transform: scale(0); transform-origin: top left; opacity: 0; top: '+bttop + 'px; left: '+btleft + 'px;';
    //   }
    // },
    toggleImage: function(){
      if(this.appData.images.length < 2) {
        this.getPhotos();
      }
      let w = window.innerWidth > 2100? window.innerWidth: 2100;
      let h = window.innerHeight > 1200? window.innerHeight: 1200;
      if ( this.appData.images.length > 0){
        let cImage =  this.appData.images.shift();
        
        cImage.url = cImage.url +'&w='+w+'&h='+h;
        this.appData.currentImage = cImage;
      }
      // preload next image
      if ( this.appData.images.length > 1){
        heavyImage.src = this.appData.images[1].url +'&w='+w+'&h='+h;
      }
    },
    toggleEffect: function(){
      this.appData.showEffect = true;
      if (this.appData.effectIndex >= this.effects.length - 1)
      {
        this.appData.effectIndex = 0;
      } else {
        this.appData.effectIndex++;
      }
    },
    resetPhotos: function() {
      //this.appData.images = [];
      this.appData.page = 1;
      this.getPhotos('changeBG');
      console.log('text change'+ this.appData.query);
    },
    getPhotos: function(action) {
      if (this.appData.query == '') {
        var mainApp = this;
        window.$.getJSON("defaultbg.json", function(data){
          mainApp.appData.images = data;
          if (action == 'changeBG' && mainApp.appData.images.length > 0) {
            mainApp.toggleImage();
          }
         
          console.log(mainApp.appData.images);

        }).fail(function(){
            console.log("An error has occurred.");
        });
        return;
      }
      var mquery = "wallpaper";
      if (this.appData.query != '') {
        mquery = this.appData.query;
      }
      console.log(mquery);
      unsplash.search.getPhotos({  query: mquery, perPage: 30, page: this.appData.page, orientation: 'landscape' }).then(result => {
        if (result.errors) {
          // handle error here
          console.log('error occurred: ', result.errors[0]);
        } else {
          const feed = result.response;
          this.appData.page++;

          // extract total and results array from response
          const { total, results } = feed;
          if (action == 'changeBG' && total > 0) {
            this.appData.images = [];
            this.appData.page = 1;
          }
          for (let i =0; i< results.length; i++) {

            this.appData.images.push({
              id: results[i].id,
              url: results[i].urls.raw,
              description: results[i].alt_description,
              link: results[i].links.html,
              author: results[i].user.name,
              location: {name: null},
              username: results[i].user.username});
          }
          if (action == 'changeBG' && this.appData.images.length > 0) {
            this.toggleImage();
          }

          // handle success here
           console.log(`received ${results.length} photos out of ${total}`);
           console.log('first photo: ', results[0]);
          // console.log(this.appData.images);
        }
      });
    }

  }
}
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css?family=Dosis');
#app {
  font-family: Dosis, Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 16px;
}

h1, h2 {
  font-weight: normal;
}

a {
  color: #42b983;
}

.only-small-screen {
  display: none;
}
.tippy-tooltip {
  font-family: Dosis, Avenir, Helvetica, Arial, sans-serif;
  background-color: rgba(51, 51, 51, 0.8);
  color: #FFF;
}

.main {
  /* make this the size of the window */
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.theImage {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #333;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-transition: all 3s ease;
  transition: all 3s ease;
  background-size: cover;
}

.image-info {
  position: absolute;
  z-index: 80;
  padding: 20px;
  bottom: 0px;
  left: 0px;
  color: #ffffffb0;
  text-align: left;
  text-shadow: 0px 0px 2px #757575db;
  a {
    color: #ffffffb0;
    text-decoration: none;
    &:hover {
      cursor: pointer;
    }
  }

}


.top-left-container {
    width: 100px;
    /* display: block; */
    position: fixed;
    top: 100px;
    left: 20px;
    padding: 0px;
    z-index: 95;
    .ob-button {
      // float: left;
    }
}

.top-right-container {
    position: absolute;
    width: 500px;
    height: 500px;
    top: 0px;
    right: 0px;
    padding: 15px;
    max-width: 100%;
    max-height: 100%;
    z-index: 70;
}

button {
  z-index: 100;
  position: relative;
  width: 200px;
  height: 25px;
  margin: 20px;
}

/* prefix with transition name */
.slide-fade-enter-active {
  opacity: 1;
  z-index: 10;
}

.slide-fade-leave-active {
  opacity: 1;
}

.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
}

.ms-nav-container {
	position: relative;
	width: 100%;
	height: 100vh;
}
// buttons
$bt-background-color: #614a0994;
$bt-text-color: #FFF;
$bt-shadow: #4c4949c7;

.ob-button {

    border-radius: 100px;
    border: none;
    background: $bt-background-color;
    color: $bt-text-color;
    outline: none;
    text-align: center;
    margin: 5px;
    width: 50px;
    height: 50px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) all 0.4s;
    &:hover {
     box-shadow: 2px 2px 5px $bt-shadow;

    }
    .icon {
      font-size: 32px;
      line-height: 50px;
    }
}
.ob-small {
  width: 40px;
  height: 40px;
  .icon {
  font-size: 28px;
    line-height: 40px;
  }
}
.rotate-hover:hover {
  cursor: pointer;
  .icon {
    transform: rotate(360deg);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
  }
}


.gen-text {
    position: absolute;
    width: 500px;
    height: 500px;
    z-index: 999;
    bottom: 5px;
    left: 5px;
}

.main-setting {
    display: block;
    position: absolute;
    bottom: 0px;
    right: 0;
    width: 75px;
    padding-bottom: 10px;
}
// ob-modal
.ob-modal {
  position: fixed;
  z-index: 90;
  width: 300px;
  height: 300px;
  background: #fff;
  top: 300px;
  left: 300px;
  max-width: 100%;
  max-height: 100%;
  border-radius: 15px;
  box-shadow: 5px 5px 15px #191919a1;
  transform: scale(0);
  opacity: 0;
  transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
  .ob-m-header {
    background: blue;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    background-image: linear-gradient(160deg, #1000ff, #499aad);
    color: white;
    height: 70px;
    line-height: 70px;
    padding-top: 5px;
    padding-left: 8px;
    text-align: left;
    position: relative;
    .ob-m-icon {
          font-size: 38px;
          float: left;
          padding-top: 15px;
          padding-right: 3px;
    }

    .ob-m-close {
        float: right;
        position: inherit;
        width: 25px;
        height: 25px;
        font-size: 22px;
        line-height: 25px;
        color: #5f5f5f;
        border-radius: 15px;
        border: none;
        padding: 0;
        padding-top: 1px;
        padding-left: 1px;
        outline: none;
        margin: 8px;
        margin-top: 4px;
    }
    
    .ob-m-action-group {
        position: absolute;
        bottom: 0;
        right: 0;
        display: block;
        .ob-m-action {
            float: right;
            width: 22px;
            height: 22px;
            font-size: 16px;
            line-height: 22px;
            color: #ffffffc7;
            border-radius: 15px;
            border: none;
            padding: 0;
            padding-top: 1px;
            padding-left: 1px;
            outline: none;
            margin: 2px;
            margin-top: 4px;
            display: inline-block;
            background: transparent;
            transition: cubic-bezier(0.165, 0.84, 0.44, 1) all 1s;
            &:hover {
              color: #FFF;
              background: #b3b3b34d;
              box-shadow: 0 0 6px #6666667a;
            }
        }
        
    }
    .ob-action-title {
        position: fixed;
        display: block;
        margin-top: 11px;
        margin-left: 5px;
    }
  }
  .ob-m-content {
    padding: 20px 10px 10px 10px;
    text-align: left;
    overflow: auto;
  }
}
// scroll bar
/* width */
::-webkit-scrollbar {
 width: 5px;
 height: 5px; 
}

/* Track */
::-webkit-scrollbar-track {
 box-shadow: none;
 border-radius: 10px;
 background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
 background: #c0c0c079;
 border-radius: 10px;
 margin: 2px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
 background: #97969677;
 
}
// form
label.ob-label {
  margin-bottom: 6px;
  margin-top: 3px;
  display: block;
}

input[type="text"] {
  box-sizing: border-box;
  width: 100%;
  height: 35px;
  padding: 4px 10px 7px 10px;
  border: 1px solid #ccc;
  border-radius: 1.5em;
  background: #fff;
  resize: none;
  outline: none;
  font-family: inherit;
  font-size: 16px;
}
input[type="text"][required]:goal {
  border-color: #00bafa;
}

// input range
input[type=range]::-webkit-slider-runnable-track {
  background: #cdcdcd;
  border: none;
  border-radius: 30px;
  width: 100%;
  height: 7.3px;
  cursor: pointer;
}
input[type=range]::-webkit-slider-thumb {
  margin-top: -3.95px;
  width: 15px;
  height: 15px;
  background: #3461cb;
  box-shadow: 2x 2px 4px #333;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  -webkit-appearance: none;
}
input[type=range] {
  width: 150px;
  margin: 5.85px 0;
  background-color: transparent;
  -webkit-appearance: none;
  outline: none;
}

// animation
.bounce-enter, .bounce-leave-to {
  opacity: 0;
}
.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);

  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}


// animation
.fade-move-enter, .fade-move-leave-to {
  opacity: 0;
}
.fade-move-enter-active {
  animation: fade-move-in .5s;
}
.fade-move-leave-active {
  animation: fade-move-out .5s;
}
@keyframes fade-move-in {
  0% {
    opacity: 0;
    left: +50px;
  }

  100% {
    opacity: 1;
    left: 0px;
  }
}
@keyframes fade-move-out {
  0% {
    opacity: 1;
    left: 0px;
  }
  100% {
    opacity: 0;
    left: -50px;
  }

}
