<template>
  <div class="note-container">
    <button class="ob-button ob-small rotate-hover" key="1" ref="notebt" content="Notes" v-tippy='{placement:"right"}'  @click="noteData.notePanel.show = !noteData.notePanel.show" ><i class="icon las la-file-contract"></i></button>
      <div class="ob-modal" v-bind:style="noteStyle" ref="notepn">
        <div class="ob-m-header">
          <i class="ob-m-icon las la-file-contract"></i>
          <span class="ob-m-title">Note</span>
          <button class="ob-m-close rotate-hover" @click="noteData.notePanel.show = !noteData.notePanel.show"><i class="icon las la-times"></i></button>
        </div>
        <div class="ob-m-content">
          <ckeditor :editor="editor" v-model="noteData.note" :config="editorConfig"></ckeditor>

        </div>
      </div>
  </div>
</template>

<script>

import appMixin from '../../mixins/appMixin';
//import Editor from '@ckeditor/ckeditor5-build-classic';
//import Editor from '@ckeditor/ckeditor5-build-inline';
import Editor from '@ckeditor/ckeditor5-build-balloon';



export default {
  name: 'note',
  mixins: [appMixin],
  components: {
  },
  data () {
    return {
        noteData: {
          notePanel: {show: false, top: 110, left: 100, width: 500, height: 400},
          note: ''
        },
        test: '',
        noteStyle: '',
        editor: Editor,
        editorConfig: {
            // The configuration of the editor.
            height: '400px',
            autoGrow_onStartup: false,
            toolbar: {
                items: [
                    'bold',
                    'italic',
                    'underline',
                    'strikethrough',
                    'todoList',
                    'bulletedList',
                    'numberedList',
                    'alignment',
                    'fontBackgroundColor',
                    'fontColor',
                    'link',
                    'insertTable',
                    'code',
                    'heading',
                ]
            }
        }
        
    }
  },

  methods: {
   resetNote: function() {
     
   }
        
  },
  watch: {
  // whenever question changes, this function will run
    noteData: {
      handler (newNoteData, oldNoteData) {
        console.log('some note changed');
        let parsed = JSON.stringify(newNoteData);
        localStorage.setItem('noteData', parsed);
      },
      deep: true
    },
    'noteData.notePanel.show': {
      handler (newAppData, oldAppData) {
        this.noteStyle = this.caculateModal('notebt','notepn',this.noteData.notePanel);
      }
    },
    
  },
  mounted: function () {
    setTimeout(() => {
      if (localStorage.getItem('noteData')) {
        try {
          console.log('topleft');
          let localData = JSON.parse(localStorage.getItem('noteData'));
          for (var name in localData) {
          //  alert(name);
            this.noteData[name] = localData[name];
          }
          let topleft = this.calculateTopLeft('notebt',this.noteData.notePanel);
          this.noteData.notePanel.top = topleft.top;
          this.noteData.notePanel.left = topleft.left;
          //this.noteData = JSON.parse(localStorage.getItem('noteData'));
        } catch(e) {
          localStorage.removeItem('noteData');
        }
      }
    }, 100);

    
  }
}
</script>

