export default {
    methods: {
        caculateModal: function(button,panel,setting) {
          if (setting.show) {
            let btleft = setting.left;
            let bttop = setting.top;
            if (setting.top+setting.height > window.innerHeight || bttop < 0) {
              bttop = 0;
            }
            if (setting.left+setting.width > window.innerWidth  || btleft < 0) {
              btleft = 0;
            }
            return 'transform: scale(1); opacity: 1; top: '+bttop + 'px; left: '+btleft + 'px; width: '+setting.width+'px;height: '+setting.height+'px;';
          } else {
            let btposition = this.$refs[button].getBoundingClientRect();
            let btleft = btposition.x + btposition.width/2;
            let bttop = btposition.y + btposition.height/2;
            return 'transform: scale(0); transform-origin: top left; opacity: 0; top: '+bttop + 'px; left: '+btleft + 'px;';
          }
        },
        calculateTopLeft: function(button,setting) {
          let btposition = this.$refs[button].getBoundingClientRect();
          let btleft = btposition.x + btposition.width + 10;
          let bttop = btposition.y;
          return {left:btleft,top:bttop};
        },
        getJSON: function(url, callback) {
          var xhr = new XMLHttpRequest();
          xhr.open('GET', url, true);
          xhr.responseType = 'json';
          xhr.onload = function() {
            var status = xhr.status;
            if (status === 200) {
              callback(null, xhr.response);
            } else {
              callback(status, xhr.response);
            }
          };
          xhr.send();
        }
    }
  };