<template>
  <div class="cembed-container">
    <button class="ob-button ob-small rotate-hover" key="1" ref="cembedbt"  content="Embed content" v-tippy='{placement:"right"}' @click="cembedData.cembedPanel.show = !cembedData.cembedPanel.show" ><i class="icon las la-file-code"></i></button>
    <div class="ob-modal cembed-modal" v-bind:style="cembedStyle" ref="cembedpn">
      <div class="ob-m-header">
        <i class="ob-m-icon  las la-file-code"></i>
        <span class="ob-m-title" v-html="cembedData.cembed[cembedData.cembedIndex]?cembedData.cembed[cembedData.cembedIndex].name:'Embed'"></span>
        <button class="ob-m-close rotate-hover" @click="cembedData.cembedPanel.show = !cembedData.cembedPanel.show"><i class="icon las la-times"></i></button>
        <div class="ob-m-action-group">
          <button class="ob-m-action rotate-hover" @click="addNew" ><i class="icon las la-plus"></i></button>
          <button class="ob-m-action rotate-hover" @click="edit"  ><i class="icon las la-pencil-alt"></i></button> 
          <!-- v-if="action=='view'" @click="action='edit'" -->
          <button class="ob-m-action rotate-hover" @click="next"><i class="icon las la-angle-right"></i></button>
          <button class="ob-m-action rotate-hover" @click="previous" ><i class="icon las la-angle-left"></i></button>
        </div>
      </div>
      <div class="ob-m-content">
        <transition name="fade-move" appear  
          enter-active-class="animated fadeInLeft"
          leave-active-class="animated fadeOutRight">
          <div class="edit-cembed-form" v-show="action=='edit'||action=='new'">
            
            <label>Name</label>
            <input type="text" v-model="cembedForm.name" :class="{'error animated tada':formError}"/>
            <label>Content</label>
            <textarea v-model="cembedForm.data" :class="{'error animated tada':formError}"></textarea>
            <div class="form-action">
              <button @click="save">{{action=='edit'?'Update':'Add'}}</button>
              <button @click="deleteItem">{{'Delete'}}</button>
              <button @click="action='view'">{{'Cancel'}}</button>
            </div>
          </div>
        </transition>
        <transition-group name="fade-move" appear tag="div"
        enter-active-class="animated fadeInLeft"
        leave-active-class="animated fadeOutRight">
          <div class="show-cembed" v-for="(item,index) in cembedData.cembed" :key="index" v-html="item.data" v-show="action=='view' && index == cembedData.cembedIndex">
            <div ></div>
          </div>
        </transition-group >
      </div>
    </div>
  </div>
</template>

<script>

import appMixin from '../../mixins/appMixin';


export default {
  name: 'cembed',
  mixins: [appMixin],
  components: {
  },
  data () {
    return {
        cembedData: {
          cembedPanel: {show: true, top: 160, left: 100, width: 350, height: 350},
          cembed: [],
          cembedIndex: 0,
         
        },
        cembedForm: {
          name: '',
          data: ''
        },
        action: 'view',
        cembedStyle: '',
        formError: false
    }
  },

  methods: {
   resetEmbed: function() {
     
   },
   next: function() {
     if (this.cembedData.cembedIndex < this.cembedData.cembed.length - 1) {
       this.cembedData.cembedIndex++;
     } else {
       this.cembedData.cembedIndex = 0;
     }
     this.action = 'view';
   },
   previous: function() {
     if (this.cembedData.cembedIndex > 0) {
       this.cembedData.cembedIndex--;
     } else {
       this.cembedData.cembedIndex =  this.cembedData.cembed.length - 1;
     }
     this.action = 'view';
   },
   addNew: function () {
     this.cembedForm.name = '';
     this.cembedForm.data = '';
     this.action = 'new';
   },
   edit: function() {
     if (this.cembedData.cembedIndex > -1 && this.cembedData.cembedIndex < this.cembedData.cembed.length) {
        this.cembedForm.name = this.cembedData.cembed[this.cembedData.cembedIndex].name;
        this.cembedForm.data = this.cembedData.cembed[this.cembedData.cembedIndex].data;
        this.action = 'edit';
     }
   },
   deleteItem: function() {
      if (this.cembedData.cembedIndex > -1 && this.cembedData.cembedIndex < this.cembedData.cembed.length) {
        var r = confirm('Are you sure want to remove this cembed '+this.cembedData.cembed[this.cembedData.cembedIndex].name+'?');
        if (r == true) {
          this.cembedData.cembed.splice(this.cembedData.cembedIndex,1);
          this.next();
        }
      }
   },
   save: function () {
     if (this.cembedForm.name == '' || this.cembedForm.data == '') {
       this.formError = true;
       setTimeout(() => { 
         this.formError = false; 
        },2000);
     }
     if (this.action == 'new') {
       let newEmbed = {name: this.cembedForm.name, data: this.cembedForm.data};
       this.cembedData.cembed.push(newEmbed);
       this.cembedData.cembedIndex = this.cembedData.cembed.length - 1;
       this.action = 'view';
     }
     if (this.action == 'edit') {
      this.cembedData.cembed[this.cembedData.cembedIndex].name = this.cembedForm.name;
      this.cembedData.cembed[this.cembedData.cembedIndex].data = this.cembedForm.data;
      this.action = 'view';
     }

   },
   caculateEmbedModal: function(button,panel,setting) {
    if (setting.show) {
      let btleft = setting.left;
      let bttop = setting.top;
      if (setting.top+setting.height > window.innerHeight || bttop < 0) {
        bttop = 0;
      }
      if (setting.left+setting.width > window.innerWidth  || btleft < 0) {
        btleft = 0;
      }
      return 'transform: scale(1); opacity: 1; top: '+bttop + 'px; left: '+btleft + 'px; width: '+setting.width+'px;height: '+setting.height+'px;';
    } else {
      let btposition = this.$refs[button].getBoundingClientRect();
      let btleft = btposition.x + btposition.width/2;
      let bttop = btposition.y + btposition.height/2;
      return 'transform: scale(1); opacity: 1; top: '+bttop + 'px; left: -'+(setting.width+30) + 'px; width: '+setting.width+'px;height: '+setting.height+'px;';
    }
  },

        
  },
  watch: {
  // whenever question changes, this function will run
    cembedData: {
      handler (newEmbedData, oldEmbedData) {
        console.log('some cembed changed');
        let parsed = JSON.stringify(newEmbedData);
        localStorage.setItem('cembedData', parsed);
      },
      deep: true
    },
    'cembedData.cembedPanel.show': {
      handler (newAppData, oldAppData) {
        this.cembedStyle = this.caculateEmbedModal('cembedbt','cembedpn',this.cembedData.cembedPanel);
      }
    },
    
  },
  mounted: function () {
    setTimeout(() => {
      if (localStorage.getItem('cembedData')) {
        try {
          let localData = JSON.parse(localStorage.getItem('cembedData'));
          for (var name in localData) {
          //  alert(name);
            this.cembedData[name] = localData[name];
          }
          //this.cembedData = JSON.parse(localStorage.getItem('cembedData'));
          let topleft = this.calculateTopLeft('cembedbt',this.cembedData.cembedPanel);
          this.cembedData.cembedPanel.top = topleft.top;
          this.cembedData.cembedPanel.left = topleft.left;
        } catch(e) {
          localStorage.removeItem('cembedData');
        }
      }
    }, 100);
    setTimeout(() => {
      this.cembedData.cembedPanel.show =false;
    },200);
    //this.cembedStyle = this.caculateEmbedModal('cembedbt','cembedpn',this.cembedData.cembedPanel);

    
  }
}
</script>

